                <tr>
                  <td
                    v-for="(title, index) in [ '規格', '零售價', '可用庫存', '採購中']"
                    :key="title+index"
                    :style="{ width: ['20%', '13%', '13%', '22%', '15%'][index] }"
                    class="py-4"
                    v-text="title"
                  />
                  <td style="width:15%">
                    <dropdown
                      :no-dot="true"
                      :title="'7日銷量'"
                      :items="[
                        { text: '7日銷量', type: 'item' },
                        { text: '30日銷量', type: 'item' },
                        { text: '60日銷量', type: 'item' },
                        { text: '90日銷量', type: 'item' },
                      ]"
                    />
                  </td>
                </tr>
              </table>
            </th>
            <th
              style="min-width:200px;"
              class="text-center py-4"
              v-text="'其他資訊'"
            />
          </template>
          <tr
            v-for="({ Id, Name, MarketName, ExchangeRateId, SupplierId, PurchaseUrl, MainId, KeyWord, Remark, Status, ProductSpecData, ProductFileData }, key) in list"
            :key="key"
          >
            <td>
              <label class="customcheckbox m-b-20">
                <input
                  ref="checkbox"
                  type="checkbox"
                  @input="onCheck({ $event, Id, key })"
                >
                <span class="checkmark" />
              </label>
            </td>
            <td>
              <div class="d-flex flex-row comment-row">
                <div
                  class="d-flex flex-column"
                >
                  <div
                    v-for="({ Id, ImageUrl }) in ProductFileData"
                    :key="Id"
                    class="p-2"
                  >
                    <img
                      :src="ImageUrl"
                      style="width:50px;height:50px;"
                    >
                  </div>
                </div>
                <div class="comment-text w-100">
                  <li>
                    <a
                      href="javascript:void(0)"
                      class="product-spec"
                      v-text="Name"
                    />
                  </li>
                  <li
                    style="color: #acacac"
                    v-text="`商品主貨號： ${MainId}`"
                  />
                  <li
                    v-for="{Name, Id} in supplierList.filter(({Id})=> Id === SupplierId)"
                    :key="Id"
                    style="color: #acacac"
                    v-text="`供應商： ${Name}`"
                  />
                  <li
                    v-if="Status"
                    style="color: #009688"
                  >
                    正常銷售 <i class="m-r-10 mdi mdi-check" />
                  </li>
                  <li
                    v-else
                    style="color: #acacac"
                  >
                    停用
                  </li>
                  <li>
                    <span
                      v-for="(str, i) in KeyWord.split(',')"
                      :key="i"
                      class="badge badge-pill badge-secondary"
                      v-text="str"
                    />
                  </li>
                </div>
              </div>
            </td>
            <td style="min-width:680px !important;width:auto;padding:0px">
              <table
                class="table-spec"
                style="min-width:680px !important;width:100%;"
              >
                <tbody>
                  <tr
                    v-for="({ Id, ItemAtrNo, SpecOne, SpecTwo, Weight, Stock, PurchasePrice, Price, Sort, Status }, index) in ProductSpecData"
                    :key="Id"
                  >
                    <td style="width:20%">
                      <li v-text="`${SpecOne},${SpecTwo}`" />
                      <li
                        style="color: #acacac"
                        v-text="`選項貨號：${ItemAtrNo}`"
                      />
                    </td>
                    <td style="width:13%">
                      <li v-text="`${PurchasePrice}`" />
                      <li
                        style="color: #acacac"
                        v-text="`成本：${Price}`"
                      />
                    </td>
                    <td style="width:13%">
                      <li v-text="`${Stock}`" />
                      <li
                        style="color: #acacac"
                        v-text="`未配貨：${Stock}`"
                      />
                    </td>
                    <td style="width:22%">
                      <li v-text="`${Stock}`" />
                      <li
                        style="color: #acacac"
                        v-text="`建議採購：${Stock}`"
                      />
                      <li
                        v-show="false"
                        name="PurchaseQuantity"
                      >
                        採購數量：
                        <input
                          type="text"
                          style="width:60px;"
                        >
                      </li>
                    </td>
                    <td
                      style="width:15%"
                      v-text="Stock"
                    />
                  </tr>
                </tbody>
              </table>
            </td>
            <td>
              <dropdown
                :title="'操作'"
                :items="[
                  { text: '修改', type: 'item', action: 'edit' },
                  { type: 'divider' },
                  { text: Status ? '停止使用' : '啟用', type: 'item', action: 'toggle' },
                  { text: '刪除', type: 'item', action: 'delete' },
                ]"
                @onMenu="onMenu({ ...$event, Id, Status, key, })"
              />
              <hr>
              備註：<br>{{ Remark || '(無備註)' }}
            </td>
          </tr>
        </data-table>
      </div>
    </div>
    <div
      v-if="showModal"
      class="modal-backdrop fade show"
    />
  </div>
</template>

<script>
import dataTable from '@/components/dataTable.vue';
import dropdown from '@/components/dropdown.vue';
import modal from '@/components/modal/index.js';
// import addLocation from '@/components/addLocation.vue';
import buttonCont from '@/components/buttonCont.vue';
import { mapActions, mapGetters } from 'vuex';

// console.log('debug: show', { show });

export default {
  name: 'Translocation',
  components: {
    dataTable,
    dropdown,
    modal,
    buttonCont,
    // addLocation,
  },
  data() {
    return {
      modifyingId: '',
      // -- modal --
      showModal: false,
      modalTitle: '',
      modalMessage: '',
      modalType: 0, // 1: 只有確認 2: 填單
      // -- modal --
      alertMessage: '',
      classBody: {
        'modal-dialog': true,
        'modal-900': true,
      },
      checkedList: [],
      inputList: {
        primary: [
          { title: '*商品名稱', placeholder: '系統預設的名稱', value: '', type: 'input' },
          { title: '上架名稱', placeholder: '上架時會採用這個名稱', value: '', type: 'input' },
        ],
        selectedRate: 0,
        SupplierId: 0,
        basic: [
          { title: '採購連結', placeholder: '採購連結', value: '', type: 'input' },
          { title: '商品主貨號', placeholder: '請輸入', value: '', type: 'input' },
        ],
        keyWords: {
          title: '關鍵字',
          placeholder: '',
          value: '',
          list: ['海運', '空運'],
        },
        specification: {
          value1: '',
          value2: '',
          list: [],
        },
        remark: [
          { title: '備註', placeholder: '', value: '', type: 'textarea' },
        ],
      },
      search: {
        status: {
          value: 0,
          list: ['全部', '正常銷售', '停止銷售', '需採購'],
        },
        text: {
          text: '',
          type: 0,
          list: ['廣泛搜尋', '商品名稱', '商品編號', '條碼', '供應商'],
        },
        BeginPrice: '',
        EndPrice: '',
        BeginPurchasePrice: '',
        EndPurchasePrice: '',
        BeginStock: '',
        EndStock: '',
      },
      activedTab: 0,
      modalFeeList: [],
      modalDraggableIndex: -1,
      // -- Buttons --
      styleData: [
        {
          forBut: { color: 1, mdi: 1, text: '新增商品' },
          forMod: { title: '新增商品', type: '2', message: '1', action: 'onCreate' },
        },
        {
          forBut: { color: 4, mdi: 5, text: '採購模式' },
          forMod: { title: '', type: '', message: 1, action: 'onBuilding' },
        },
        {
          forBut: { color: 2, mdi: 2, text: '正常銷售' },
          forMod: { title: '', type: '', message: 2, action: 'onEnableAll' },
        },
        {
          forBut: { color: 2, mdi: 3, text: '停止銷售' },
          forMod: { title: '', type: '', message: 3, action: 'onStopAll' },
        },
        {
          forBut: { color: 3, mdi: 4, text: '刪除' },
          forMod: { title: '', type: '', message: 4, action: 'onDeleteAll' },
        },
      ],
    };
  },
  computed: {
    ...mapGetters({
      exchangeList: 'ExchangeRate/list',
      supplierList: 'Supplier/list',
      imageList: 'Image/list',
      list: 'Product/list',
      pagination: 'Product/pagination',
    }),
  },
  watch: {
    'inputList.keyWords.value': function (val, old) {
      this.splitKeyWords();
    },
    'inputList.supplier.value': function (val, old) {
      this.filterSupplier();
    },
  },
  created() {},
  mounted() {
    console.log('debug: mounted -> window.pd = this', window.pd = this);
    this.callSearch();
    this.getExchangeList({
      Page: 1,
      PageLimit: 50,
    });
    this.getSupplierList({
      Page: 1,
      PageLimit: 50,
    });
  },
  methods: {
    ...mapActions({
      getExchangeList: 'ExchangeRate/read',
      getSupplierList: 'Supplier/read',
    }),
    ...mapActions('Image', {
      uploadImage: 'create',
      pushImages: 'update',
      // pushImages: 'modify',
      deleteImage: 'deleted',
    }),
    ...mapActions('Product', {
      getList: 'read',
      addLocation: 'create',
      changeSetting: 'modify',
      changeStatus: 'update',
      deleteProduct: 'deleted',
    }),
    ...mapActions('Modal', {
      createModal: 'create',
    }),
    callModal({ title, type, message, action }) {
      if (['onCreate', 'onEdit'].includes(action)) {
        document.body.classList.add('modal-open');
        document.body.style.paddingRight = '17px';
        this.showModal = true;
        this.modalTitle = title || '提示訊息';
        this.modalMessage = message || '';
        this.modalType = type || 0;
        this.action = action || 'closeMod';
        this.alertMessage = '';
      } else if (typeof this[action] === 'function') {
        this[action]();
        // onEnableAll
        // onStopAll
        // onDeleteAll
      }
    },
    onModalAdd() {
      const { weightSub } = this.inputList;
      const {
        0: { value: beginWeight },
        2: { value: endWeight },
        3: { value: fee },
      } = weightSub;
      const rateId = this.selectedRate.sub;
      const { CurrencyName: name, Rate: rate } = this.exchangeList.filter(({ Id }) => Id === rateId)[0] || {};
      this.modalFeeList.push({ beginWeight, endWeight, fee, name, rate, rateId });
    },
    onConfirm() {
      const action = this[this.action];
      console.log('debug: onConfirm -> action', action);
      if (typeof action === 'function') {
        action();
      }
    },
    closeMod() {
      document.body.classList.remove('modal-open');
      document.body.style.paddingRight = null;
      this.showModal = false;
      this.modalMessage = '';
      this.alertMessage = '';
      // Object.keys(this.inputList).forEach((key) => {
      //   const item = this.inputList[key];
      //   if (typeof item === 'string') {
      //     this.inputList[key] = '';
      //   }
      // });
      const { primary, basic, keyWords, specification, remark } = this.inputList;
      specification.value1 = '';
      specification.value2 = '';
      // this.inputList.SupplierId = 0;
      [...primary, ...basic, keyWords, specification, ...remark]
        .forEach((item) => {
          if (Object.hasOwnProperty.call(item, 'value')) {
            item.value = '';
          }
          if (Object.hasOwnProperty.call(item, 'list')) {
            item.list.splice(0);
          }
        });
      this.modalFeeList.splice(0);
      this.deleteImage({});
    },
    // 由輸入資料過濾供應商資料
    filterSupplier() {
      this.inputList.supplier.Id = null;
      const { value } = this.inputList.supplier;
      if (value) {
        const reg = new RegExp(value);
        const list = this.supplierList.filter(({ Id, Name }) => reg.test(Name));
        if (list.length) {
          this.inputList.supplier.Id = list[0].Id;
          this.inputList.supplier.value = list[0].Name;
        } else {
          this.inputList.supplier.value = '';
        }
      }
    },
    // 由輸入資料產生關鍵字
    splitKeyWords() {
      const { value, list } = this.inputList.keyWords;
      const words = value.split(/[,\s]/g).filter((s) => s);
      if (words.length) {
        words.forEach((str) => {
          if (list.indexOf(str) === -1) {
            list.push(str);
          }
        });
        this.inputList.keyWords.value = '';
      }
    },
    // 加入規格
    onAddSpec() {
      const { specification } = this.inputList;
      const { value1, value2, list } = specification;
      if (value1) {
        specification.value1 = '';
        specification.value2 = '';
        list.push({
          ItemAtrNo: '',
          SpecOne: value1,
          SpecTwo: value2,
          Weight: '',
          Stock: 20,
          PurchasePrice: '',
          Price: '',
          Sort: list.length,
          Status: true,
        });
      }
    },
    // 規格選單
    onSpecMenu(payload) {
      const { list } = this.inputList.specification;
      const { action, item, index } = payload;
      if (action === 'enable') {
        item.Status = true;
      } else if (action === 'disable') {
        item.Status = false;
      } else if (action === 'delete') {
        list.splice(index, 1);
      } else if (action === 'up' && index > 0) {
        list.splice(index, 1);
        list.splice(index - 1, 0, item);
      } else if (action === 'down' && index < list.length - 1) {
        list.splice(index, 1);
        list.splice(index + 1, 0, item);
      }
    },
    // 上傳圖片
    onFile() {
      // this.$refs.file
      const formData = new FormData();
      const { file } = this.$refs;
      const fileData = file?.files?.[0];
      if (fileData) {
        formData.append('fileData', fileData);
        this.uploadImage(formData)
          .then((result) => {
            file.value = '';
          })
          .catch((err) => {
            file.value = '';
          });
      }
    },
    getSearchPaylaod() {
      const { search, pagination } = this;
      const {
        status: { value: SearchStatus },
        text: { type: SearchTextType, text: SearchText },
        BeginPrice, EndPrice, BeginPurchasePrice, EndPurchasePrice, BeginStock, EndStock,
      } = search;
      return {
        SearchStatus,
        SearchTextType,
        SearchText,
        BeginPrice,
        EndPrice,
        BeginPurchasePrice,
        EndPurchasePrice,
        BeginStock,
        EndStock,
        pagination,
      };
    },
    resetSearch() {
      const { search } = this;
      search.status.value = 0;
      search.text.text = '';
      search.BeginPrice = '';
      search.EndPrice = '';
      search.BeginPurchasePrice = '';
      search.EndPurchasePrice = '';
      search.BeginStock = '';
      search.EndStock = '';
    },
    // 搜尋功能
    callSearch() {
      this.getList({
        ...this.getSearchPaylaod(),
        pagination: {
          Page: 1,
          PageLimit: 10,
        },
      });
    },
    onTopButton($event) {
      const { action } = $event;
      if (action === 'onBuilding') {
        this.createModal({
          message: '功能建構中',
          showCancel: false,
        }).then(() => {});
      } else {
        this.callModal($event);
      }
    },
    getUploadPaylaod() {
      const { inputList } = this;
      const {
        primary: [
          { value: Name },
          { value: MarketName },
        ],
        selectedRate: ExchangeRateId,
        SupplierId,
        basic: [
          { value: PurchaseUrl },
          { value: MainId },
        ],
        keyWords: {
          list: KeyWordList,
        },
        remark: [
          { value: Remark },
        ],
        specification: {
          list: specificationList,
        },
      } = inputList;
      const KeyWord = KeyWordList.join(',');
      const ProductSpecData = specificationList.map((item, index) => ({
        ...item,
        Sort: index,
      }));
      const ProductFileData = this.imageList.map(({ FileId, FileUrl }, index) => ({
        Id: FileId,
        ImageUrl: FileUrl,
        FileType: 0,
        Sort: index,
      }));
      return {
        Name,
        MarketName,
        ExchangeRateId,
        SupplierId,
        PurchaseUrl,
        MainId,
        KeyWord,
        Remark,
        Status: true,
        ProductSpecData,
        ProductFileData,
      };
    },
    onCreate() {
      const {
        Name,
        MarketName,
        ExchangeRateId,
        SupplierId,
        PurchaseUrl,
        MainId,
        KeyWord,
        Remark,
        Status,
        ProductSpecData,
        ProductFileData,
      } = this.getUploadPaylaod();

      if (Name && ExchangeRateId && SupplierId) {
        // if (true) {
        this.addLocation({
          Name,
          MarketName,
          ExchangeRateId,
          SupplierId,
          PurchaseUrl,
          MainId,
          KeyWord,
          Remark,
          Status,
          ProductSpecData,
          ProductFileData,
        })
          .then((result) => {
            this.closeMod();
            this.createModal({
              message: '新增成功',
              showCancel: false,
            }).then(() => {});
            // this.modalMessage = '新增成功';
            this.getList(this.getSearchPaylaod());
          })
          .catch((err) => {
            this.alertMessage = err.message || '不明錯誤';
          });
      } else {
        this.alertMessage = '所有星號(*)及匯率必填';
      }
    },
    onEdit() {
      const {
        Name,
        MarketName,
        ExchangeRateId,
        SupplierId,
        PurchaseUrl,
        MainId,
        KeyWord,
        Remark,
        Status,
        ProductSpecData,
        ProductFileData,
      } = this.getUploadPaylaod();

      if (Name && ExchangeRateId && SupplierId) {
        // if (true) {
        this.changeSetting({
          Id: this.modifyingId,
          data: {
            Name,
            MarketName,
            ExchangeRateId,
            SupplierId,
            PurchaseUrl,
            MainId,
            KeyWord,
            Remark,
            Status,
            ProductSpecData,
            ProductFileData,
          },
        })
          .then((result) => {
            this.closeMod();
            this.createModal({
              message: '修改成功',
              showCancel: false,
            }).then(() => {});
            // this.modalMessage = '新增成功';
            this.getList(this.getSearchPaylaod());
          })
          .catch((err) => {
            this.alertMessage = err.message || '不明錯誤';
          });
      } else {
        this.alertMessage = '所有星號(*)必填';
      }
    },
    onMenu({ text, type, index, action, Id, Status, key }) {
      if (action === 'edit') {
        const { ExchangeRateId, Id, KeyWord, MainId, MarketName, Name, ProductFileData, ProductSpecData, PurchaseUrl, Remark, Status, SupplierId } = this.list[key];

        const { inputList } = this;
        const { primary, basic, keyWords, specification, remark } = inputList;

        inputList.selectedRate = ExchangeRateId;
        inputList.SupplierId = SupplierId;

        primary[0].value = Name;
        primary[1].value = MarketName;

        primary[0].basic = PurchaseUrl;
        primary[1].basic = MainId;

        keyWords.list.splice(0);
        KeyWord.split(',').forEach((str) => {
          keyWords.list.push(str);
        });

        specification.list.splice(0);
        ProductSpecData.forEach((item) => {
          specification.list.push({ ...item });
        });

        ProductFileData.forEach(({ Id, ImageUrl }) => {
          this.pushImages({
            FileId: Id,
            FileUrl: ImageUrl,
          });
        });

        this.callModal({
          message: '',
          title: '修改商品',
          action: 'onEdit',
          type: 2,
        });
        this.modifyingId = Id;
      } else if (action === 'delete') {
        // 下拉選單刪除
        this.createModal({
          message: '確定刪除此項目？',
          // showCancel: false
        }).then(() => {
          this.deleteProduct(Id)
            .then((result) => {
              this.createModal({
                message: '刪除成功',
                showCancel: false,
              }).then(() => {});
              this.getList(this.getSearchPaylaod());
            })
            .catch((err) => {
              this.createModal({
                message: err.message || '不明錯誤',
                showCancel: false,
              }).then(() => {});
            });
        });
        // 下拉選單刪除
      } else if (action === 'toggle') {
        // 下拉選單 停用/啟用
        this.createModal({
          message: `確定${Status ? '停用' : '啟用'}此項目？`,
          // showCancel: false
        }).then(() => {
          this.changeStatus({
            Id,
            Status: !Status,
          })
            .then((result) => {
              this.createModal({
                message: '修改成功',
                showCancel: false,
              }).then(() => {});
              this.getList(this.getSearchPaylaod());
            })
            .catch((err) => {
              this.createModal({
                message: err.message || '不明錯誤',
                showCancel: false,
              }).then(() => {});
            });
        });
        // 下拉選單 停用/啟用
      }
    },
    onCheck({ $event, Id, key }) {
      const { checked } = $event.target;
      console.log('debug: onCheck -> checked', checked);
      if (checked && !this.checkedList.includes(Id)) {
        this.checkedList.push(Id);
      }
      if (!checked && this.checkedList.includes(Id)) {
        const index = this.checkedList.indexOf(Id);
        // console.log('debug: onCheck -> index', index);
        if (index > -1) {
          this.checkedList.splice(index, 1);
        }
      }
    },
    onAllCheck(checked) {
      if (checked) {
        const { checkbox } = this.$refs;
        const list = Array.isArray(checkbox)
          ? checkbox
          : [checkbox];
        list.forEach((el) => {
          el.checked = true;
        });
        this.checkedList.splice(0);
        this.list.forEach(({ Id }) => {
          this.checkedList.push(Id);
        });
      } else {
        this.clearAllchecked();
      }
    },
    clearAllchecked() {
      const { checkbox } = this.$refs;
      const list = Array.isArray(checkbox)
        ? checkbox
        : [checkbox];
      list.forEach((el) => {
        el.checked = false;
      });
      this.checkedList.splice(0);
    },
    onBuilding() {
      this.createModal({
        message: '不明錯誤',
        showCancel: false,
      }).then(() => {});
    },
    onEnableAll() {
      if (this.checkedList.length) {
        this.createModal({
          message: '確定將全部勾選項目改為正常銷售？',
          // showCancel: false
        }).then(() => {
          const promiseList = [];
          this.checkedList.forEach((Id) => {
            promiseList.push(this.changeStatus({
              Id,
              Status: true,
            }));
          });
          Promise.allSettled(promiseList)
            .then((result) => {
              // this.closeMod();
              this.createModal({
                message: '修改成功',
                showCancel: false,
              }).then(() => {});
              // this.modalMessage = '修改成功';
              this.getList(this.getSearchPaylaod());
            })
            .catch((err) => {
              this.createModal({
                message: err.message || '不明錯誤',
                showCancel: false,
              }).then(() => {});
              // this.modalMessage = err.message || '不明錯誤';
            });
        });
      } else {
        this.createModal({
          message: '請先勾選要設定的項目',
          showCancel: false,
        }).then(() => {});
      }
    },
    onStopAll() {
      if (this.checkedList.length) {
        this.createModal({
          message: '確定將全部勾選項目改為停止銷售？',
          // showCancel: false
        }).then(() => {
          const promiseList = [];
          this.checkedList.forEach((Id) => {
            promiseList.push(this.changeStatus({
              Id,
              Status: false,
            }));
          });
          Promise.allSettled(promiseList)
            .then((result) => {
              // this.clearAllchecked();
              // this.closeMod();
              this.createModal({
                message: '修改成功',
                showCancel: false,
              }).then(() => {});
              // this.modalMessage = '修改成功';
              this.getList(this.getSearchPaylaod());
            })
            .catch((err) => {
              // this.clearAllchecked();
              this.createModal({
                message: err.message || '不明錯誤',
                showCancel: false,
              }).then(() => {});
              // this.modalMessage = err.message || '不明錯誤';
            });
        });
      } else {
        this.createModal({
          message: '請先勾選要設定的項目',
          showCancel: false,
        }).then(() => {});
      }
    },
    onDeleteAll() {
      if (this.checkedList.length) {
        this.createModal({
          message: '確定刪除全部勾選的項目？',
          // showCancel: false
        }).then(() => {
          const promiseList = [];
          this.checkedList.forEach((Id) => {
            promiseList.push(this.deleteProduct(Id));
          });
          Promise.allSettled(promiseList)
            .then((result) => {
              this.clearAllchecked();
              // this.closeMod();
              this.createModal({
                message: '刪除成功',
                showCancel: false,
              }).then(() => {});
              // this.modalMessage = '修改成功';
              this.getList(this.getSearchPaylaod());
            })
            .catch((err) => {
              this.clearAllchecked();
              this.createModal({
                message: err.message || '不明錯誤',
                showCancel: false,
              }).then(() => {});
              // this.modalMessage = err.message || '不明錯誤';
            });
        });
      } else {
        this.createModal({
          message: '請先勾選要設定的項目',
          showCancel: false,
        }).then(() => {});
      }
    },
    onPage(Page) {
      if (+Page !== +this.pagination.Page) {
        this.getList({
          ...this.getSearchPaylaod(),
          pagination: {
            ...this.pagination,
            Page,
          },
        });
        this.clearAllchecked();
      }
    },
    onDragEnable(index) {
      this.modalDraggableIndex = index;
    },
    onDragDisable() {
      this.modalDraggableIndex = -1;
    },
    onDrop(index) {
      // console.log(`debug: onDrop -> e`, e)
      const target = this.modalDraggableIndex;
      const { imageList } = this;
      const clonedList = [...imageList];
      this.deleteImage({});
      [clonedList[target], clonedList[index]] = [clonedList[index], clonedList[target]];
      // console.log(`debug: onDrop -> clonedList`, clonedList)
      clonedList.forEach(({ FileId, FileUrl }) => {
        this.pushImages({
          FileId,
          FileUrl,
        });
      });
      // this.modalDraggableIndex = -1;
    },
  },
};

</script>

